<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm">
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
            <el-row :gutter="15">
                <el-col :span="12">
                    <el-form-item label="案卷名称" prop="name">
                        <el-input v-model="dialogForm.name" :maxlength="25" placeholder="请填写案卷名称"></el-input>
                    </el-form-item>
                    <el-form-item label="案卷开始日期" prop="startDate">
                        <el-date-picker v-model="dialogForm.startDate" type="date" placeholder="请选择日期"></el-date-picker>
                    </el-form-item>  
                </el-col>
                <el-col :span="12">
                    <el-form-item label="案卷编号" prop="caseNo">
                        <el-input v-model="dialogForm.caseNo" :maxlength="25" placeholder="请填写案卷编号"></el-input>
                    </el-form-item>
                    <el-form-item label="侦查终结日期" prop="endDate">
                        <el-date-picker v-model="dialogForm.endDate" type="date" placeholder="请选择日期"></el-date-picker>
                    </el-form-item>  
                </el-col>
            </el-row>
            <el-row :gutter="15">
                <el-col :span="12">
                    <el-form-item label="地址编码" prop="address">
                        <el-input v-model="dialogForm.address" :maxlength="32" placeholder="请填写地址编码"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                </el-col>
            </el-row>
            <el-row :gutter="15">
                <el-col :span="24">
                    <el-form-item label="简要情况" prop="caseDesc">
                        <el-input v-model="dialogForm.caseDesc" type="textarea" :rows="3" :maxlength="4000" placeholder="请填写简要情况"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/district_check_service.js";

export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      disCodeOption: [],
      dialogForm: {
        name:"",
        caseNo:"",
        startDate:"",
        endDate:"",
        caseDesc:"",
        address:"",
        districtCode:[]
      },
      teamRules: {
        name: [{ required: true, message: "请输入案卷名称", trigger: "blur" }],
        caseNo: [
          { required: true, message: "请输入案卷编号", trigger: "blur" }
        ],
        startDate: [
          { required: true, message: "请选择案卷开始日期", trigger: "change" }
        ],
        districtCode: [
          { required: true, message: "请选择区域编码", trigger: "change" }
        ]
      }
    };
  },
  watch: {
      
    },
  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  
  methods: {
   
    /** 保存 */
    submitForm() {
        this.$refs["dialogForm"].validate(valid => {
          if (valid) {
            let param = _.cloneDeep(this.dialogForm);
            param.startDate = util.time.formatDate(param.startDate,"YYYY-MM-DD");
            param.endDate = util.time.formatDate(param.endDate,"YYYY-MM-DD");
            param.dicMap && delete param.dicMap;
            if (this.isAdd == true) {

              // 添加 命案基本信息
              service.requestSaveCaseInfo(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "命案基本信息添加成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            } else {
              service.requestUpdateCaseInfo(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "命案基本信息修改成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            }
          } else {
            console.log("error submit!!");
            return false;
          }
        });
       
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.dialogForm = {};
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-dialog .el-select {
  width: 100%;
}
.add-dialog .el-cascader {
  width: 100%;
}
</style>