import request from '@/plugin/axios'

const service = {
    /**
     * 获取网格编码
     * 
     * @param {any} successFunc 回调函数
     */
    requestGridCode(successFunc) {
        request({
            url: "/grid/rstu/find/all/code",
            method: "get",
        }).then((res) => {
            res.region = _.sortBy(res.region, "code");
            let code = constants.REGION.CODE;
            let region = {
                code: code,
                label: constants.REGION.NAME,
                children: []
            };
            let itemONE = {};
            switch (code && code.length) {
                case 4:
                    // 组织县级区域
                    itemONE = _.map(res.region, county => {
                        return {
                            code: county.code,
                            label: county.name,
                            children: []
                        };
                    })
                    // 组织街道办区域
                    _.map(itemONE, item => {
                        item.children = _.map(_.filter(res.street, street => {
                            return street.code.indexOf(item.code) >= 0
                        }), street => {
                            return {
                                code: street.code,
                                label: street.name,
                                children: []
                            };
                        })
                    })
                    // 组织社区区域
                    _.map(itemONE, county => {
                        _.map(county.children, item => {
                            item.children = _.map(_.filter(res.community, community => {
                                return community.code.indexOf(item.code) >= 0
                            }), community => {
                                return {
                                    code: community.code,
                                    label: community.name,
                                    children: []
                                };
                            })
                        })
                    })
                    // 组织单元网格区域
                    _.map(itemONE, county => {
                        _.map(county.children, street => {
                            _.map(street.children, item => {
                                item.children = _.map(_.filter(res.unit, unit => {
                                    return unit.code.indexOf(item.code) >= 0
                                }), unit => {
                                    return {
                                        code: unit.code,
                                        label: unit.name
                                    };
                                })
                            })
                        })
                    })
                    break;
                case 6:
                    // 组织街道办
                    itemONE = _.map(_.filter(res.street, street => {
                        return street.code.indexOf(code) >= 0
                    }), street => {
                        return {
                            code: street.code,
                            label: street.name,
                            children: []
                        };
                    });
                    // 组织社区
                    _.map(itemONE, item => {
                        item.children = _.map(_.filter(res.community, community => {
                            return community.code.indexOf(item.code) >= 0
                        }), community => {
                            return {
                                code: community.code,
                                label: community.name,
                                children: []
                            };
                        })
                    })

                    // 组织网格
                    _.map(itemONE, county => {
                        _.map(county.children, item => {
                            item.children = _.map(_.filter(res.unit, unit => {
                                return unit.code.indexOf(item.code) >= 0
                            }), unit => {
                                return {
                                    code: unit.code,
                                    label: unit.name,
                                };
                            })
                        })
                    })
                    break;
                case 9:
                    // 组织社区
                    itemONE = _.map(_.filter(res.community, community => {
                        return community.code.indexOf(code) >= 0
                    }), community => {
                        return {
                            code: community.code,
                            label: community.name,
                            children: []
                        };
                    })
                    // 组织网格
                    _.map(itemONE, item => {
                        item.children = _.map(_.filter(res.unit, unit => {
                            return unit.code.indexOf(item.code) >= 0
                        }), unit => {
                            return {
                                code: unit.code,
                                label: unit.name
                            };
                        })
                    })
                    break;
                case 12:
                    // 组织网格
                    itemONE = _.map(_.filter(res.unit, unit => {
                        return unit.code.indexOf(code) >= 0
                    }), unit => {
                        return {
                            code: unit.code,
                            label: unit.name,
                        };
                    })
                    break;
                default:
                    break;
            };
            region.children = itemONE;
            successFunc(region);
        })
    },
    /**
     * 查询字典表目录
     * 
     * @param {any} successFunc 回调函数
     */
    requestDicCatalog(successFunc) {
        request({
            url: "/buss/info/dictionary/catalog/findAll",
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 通过字典表目录ID查询字典表
     * 
     * @param {any} catalogId 字典表目录ID
     * @param {any} successFunc 回调函数
     */
    requestDicType(catalogId, successFunc) {
        request({
            url: "/buss/info/dictionary/findDicByCatalogId?catalogId="+ catalogId,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 通过实体类名和属性名统计
     * 
     * @param {any} param 查询参数
     * @param {any} successFunc 回调函数
     */
    requestByEntityNames(param, successFunc) {
        request({
            url: "/compre/statis/common/district?districtCode="+ param.districtCode +"&entityNames="+ param.entityNames,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 根据字段属性占比查询
     * 
     * @param {any} param 查询参数
     * @param {any} successFunc 回调函数
     */
    requestByFieldName(param, successFunc) {
        request({
            url: "/compre/statis/common/field?districtCode="+ param.districtCode +"&entityName="+ param.entityName+"&fieldName="+ param.fieldName,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 区域属性值和的统计
     * 
     * @param {any} param 统计参数
     * @param {any} successFunc 回调函数
     */
    requestByEntityAndFieldSum(param, successFunc) {
        request({
            url: "/compre/statis/common/district/field/sum?districtCode="+ param.districtCode +"&entityName="+ param.entityName+"&fieldName="+ param.fieldName,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },

    /**
     * 查询基本人口的综治信息
     * 
     * @param {any} peoId 基本人口 ID
     * @param {any} successFunc 回调函数
     */
    requestFocusPeople(peoId, successFunc) {
        request({
            url: "/compre/hp/people/find/compreInfo?id="+ peoId,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 查询重点关注人信息
     * 
     * @param {any} param 查询参数  entity人员类型  focusId人员ID
     * @param {any} successFunc 回调函数
     */
    requestFocus(param, successFunc) {
        request({
            url: "/compre/"+ param.entity +"/find?id="+ param.focusId,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
}

export default service;